












const app = getApp();
export default {
	data() {
		return {
			inHeight:'',
			inWidth:'',
			jing:0,
			wei:0,
			// 地图坐标点
			covers:[{
				latitude:0,
				longitude:0,
				id:0,
				iconPath:'../../static/image/path.png'
				}
			],
			// 网店信息
			wdInfo:[],
			// 地区#ifdef
			
		}
	},
	created() {
		var that = this;
		that.inHeight = uni.getStorageSync('infoHeight');
		that.inWidth = uni.getStorageSync('infoWidth');
	},
	onLoad() {
		var that = this;
		uni.showLoading({
			title:'数据加载中...',
		}) 
		uni.request({
			url:that.http+'index/getAreaList',
			header:that.header,
			method:'POST',
			success: (res) => {
				// 现获取地区id
				// console.log(res)
				if(res.data.code!=200){
					uni.showToast({
						title:res.data.data.mes,
						icon:'none'
					})
				}else{
					that.id = res.data.data[0].id;
					uni.request({
						url:that.http+'index/getNetList',
						header:that.header,
						method:'POST',
						data:{
							area_id:res.data.data[0].id
						},
						success: (result) => {
							uni.hideLoading()
							if(res.data.code==200){
								that.wdInfo = result.data.data;
								for(var i of result.data.data){
									var arr = {};
									arr.longitude = i.jing
									arr.latitude = i.wei
									arr.iconPath = '../../static/image/wd.png'
									arr.id = i.id
									that.covers.push(arr)
								}
							}else{
								uni.showToast({
									title:'获取网点信息失败，请刷新后重试!',
									icon:'none'
								})
							}
						},
						fail() {
							uni.showToast({
								title:'请求网点信息失败，请刷新后重试!',
								icon:'none'
							})
						}
					})
				}
			},
			fail(){
				uni.hideLoading()
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	},
	onShow() {
		var that = this;
		// 获取用户的地理位置
		uni.getLocation({
			type:'gcj02',		//国测局坐标比较标准
			success:function(res){
				that.jing = res.longitude;
				that.wei = res.latitude;
				that.covers[0].latitude = res.latitude;
				that.covers[0].longitude = res.longitude;
			}
		})
	},
	// 自定义按钮点击事件
	onNavigationBarButtonTap(e){
		if(e.index==1){
			uni.navigateTo({
				url:'../userPages/notice/notice'
			});
		}else if(e.index==0){
			if (uni.getStorageSync('token') == '' || uni.getStorageSync('token') == undefined) {
				uni.showModal({
					title: '温馨提示！',
					content: '请先前往登录!',
					confirmText: '登录',
					success(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '../loginPages/login/login'
							});
						}
					}
				})
			} else{
			uni.navigateTo({
				url:'../user/user'
			});
			}
		}
	},
	methods: {
		login(){
			var that = this;
			if(uni.getStorageSync('token') == '' || uni.getStorageSync('token')==undefined){
				uni.showModal({
					title:'温馨提示！',
					content:'请先前往登录!',
					confirmText:'登录',
					success(res){
						if(res.confirm){	
							uni.navigateTo({
								url: '../loginPages/login/login'
							});
						}
					}
				})
			}else{
				uni.navigateTo({
					url:'../userPages/wdList/wdList?id='+that.id
				})
			}
		},
		detail(e){
			console.log(e)
			uni.navigateTo({
				url:'../userPages/wdDetail/wdDetail?id='+e.mp.markerId
			})
		}
	}
}
